import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";

import Test from "./Test"
import logo from './scl.png';

import styles from "./Navbar.module.css";

const useStyles = makeStyles((theme) => ({
  root: {
    position: "fixed",
    backgroundColor: "#000066",
    width: "100%",
    paddin: 0
  },
  menuButton: {
    marginRight: theme.spacing(0),
  },
  title: {

  },
  ava: {
    right: "0",
    position: "relative"

  },
}));

export default function Navbar(props) {

  const classes = useStyles();


  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  //to display avatar in logged in state
  const [isSignedIn, setIsSigned] = useState(false);
  const [name, setName] = useState("");
  const [image, setImage] = useState("");

  useEffect(() => {

    if (localStorage.getItem("email")) {
      setIsSigned(true);
      setName(localStorage.getItem("name"));
      setImage(localStorage.getItem("image"));
    }
  });

  return (
    <div className={classes.root} style={{}}>
      <AppBar position="static" className={styles.navv} style={{ padding: "0" }}>
        <Toolbar className={styles.navcontent} style={{ padding: "0", width: "100%" }}>
          &nbsp;
          <img src={logo} alt="." style={{ width: "45px", borderRadius: "50px", boxShadow: "1px 1px 2px #000" }} />
          <Typography
            variant="h6"
            className={classes.title}
            style={{ fontFamily: "'Russo One', sans-serif" }}
          >

            &nbsp;&nbsp;SCREENCAST 2020
          </Typography>

          <div className={classes.ava}>
            {isSignedIn ? (
              <div>

                <Avatar
                  alt={name}
                  src={image}
                  className={styles.ava}
                />

              </div>
            ) : (
                <div>

                </div>
              )}
          </div>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="white"
            aria-label="menu"
            style={{ padding: "0", right: "0", position: "absolute" }}
          >
            <Test />
          </IconButton>
        </Toolbar>

      </AppBar>
      <div>

      </div>


    </div>
  );
}
